  align-items: center;
}

.FileLoader_input__2xdRl {
  overflow: hidden;
  position: absolute;
  z-index: -1;
  width: 0px;
  height: 0px;
  visibility: hidden;
}

.FileLoader_file__2F9P_ {
  margin-top: .5em
}
